body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background: #2a3132; 
  }
  .box {
    width: 300px;
    padding: 40px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background: #FEFEFE;
    border: none;
    border-radius: 25px;
    text-align: center;
  }
  h1{
    color: #000000;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 5px;
  }
  /************** 
   Input Fields
  ***************/
  #datefrom{
    border: 2px solid #0097e6;
    background: none;
    display: block;
    margin: 20px auto;
    padding: 15px 45px;
    width: 100px;
    outline: none;
    color: #000000;
    border-radius: 25px;
    text-align: center;
    transition: 250ms width ease, 250ms border-color ease;
  }

  #dateto{
    border: 2px solid #0097e6;
    background: none;
    display: block;
    margin: 20px auto;
    padding: 15px 45px;
    width: 100px;
    outline: none;
    color: #000000;
    border-radius: 25px;
    text-align: center;
    transition: 250ms width ease, 250ms border-color ease;
  }

  #land{
    border: 2px solid #0097e6;
    background: none;
    display: block;
    margin: 20px auto;
    padding: 15px 45px;
    width: 200px;
    outline: none;
    color: #000000;
    border-radius: 25px;
    text-align: center;
    transition: 250ms width ease, 250ms border-color ease;
  }
  /************** 
     On Hover
  ***************/
  #date:hover{
    width: 210px;
  }
  /************** 
     On Focus
  ***************/
  #date:focus{
    width: 230px;
    border-color: #6fb98f;
  }
  /************** 
   Submit Button
  ***************/
  #submit{
    border: 2px solid #2ecc71;
    background: #2ecc71;
    display: block;
    margin: 20px auto;
    text-align: center;
    padding: 15px 40px;
    outline: none;
    color: #000000;
    border-radius: 25px;
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 200;
    transition: 250ms background ease;
    -webkit-transition: 250ms background ease;
    -moz-transition: 250ms background ease;
    -o-transition: 250ms background ease;
  }
  /************** 
     On Hover
  ***************/
  #submit:hover{
    background: #2ecc99;
  }